@import "./variables.scss";

.global-cards {
  display: grid;
  grid-template-columns: auto auto auto auto;

  gap: 5vh;
  margin: auto;
  justify-content: center;
  justify-self: center;
  justify-items: center;
}

.a {
  grid-row: span 2;
}
.card {
  min-width: 30vh;
  max-width: 100%;
  height: 25vh;
  display: inline-block;
  margin: auto;
  position: relative;
  border-radius: 20px;
}
.content {
  text-align: center;
  border-radius: 20px;
  margin: 5vh 0 0 0;
  height: 25vh;
  background: $neutral-dark-blue;
  color: white;
  padding: 3vh;
  font-family: "Rubik", sans-serif;
}
.activity {
  position: relative;
  text-align: left;
  font-size: 2.5vh;
  font-weight: 600;
  margin-bottom: 3vh;
}
.spent-time{
    font-size: 7vh;
    text-align: left;
}
.activity::before {
  content: "";
  background: url(../images/icon-ellipsis.svg) no-repeat;
  background-position: right;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.work::before {
  background: $orange url(../images/icon-work.svg) no-repeat;
  z-index: -2;
  background-position: 90%;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 7vh;
  position: absolute;
  border-radius: 15px 15px 0 0;
}

